.product {
  width: 74%;
  background: rgba(24,26,28,.5);
  border: 1px solid orange;
  border-radius: 0.7rem;
  overflow: hidden;
}
.product__sort {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(24,26,28,.5);
  padding: .5%;
  border-bottom: 1px solid orange;
}
.product__list {
  padding: .5%;
  display: flex;
  flex-wrap: wrap;
}
.not__found {
  text-align: center;
  font-size: 200%;
  margin-top: 10%;
  color: wheat;
}

.Stat {
  color: wheat;
}

.SearchInput {
  background-color: #181a1c;
  width: 140%;
  border: 1px solid orange;
  color: orange;
  border-radius: 2rem;
  padding: 0.2rem;
  outline: none;
  font-size: 80%;
  text-align: center;
}
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance:none;
  appearance:none;
  height:1rem;
  width:1rem;
  background-image:url(data:image/svg+xml;base64,);
  background-size:100% 100%;
  background-repeat:no-repeat;
  background-color:orange;
  border-radius:100%;
  cursor:pointer
}

.ViewMode {
  display: flex;
  align-items: center;
  justify-content: space-between;
}